@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li {
  @apply px-3;
}
.bg-primary{
  background:#0a0f1c;
}
.bg-secondary{
  background: #0a0f1c;
}
.mobile-nav{
  @apply fixed top-10 left-0 bg-gray-800 w-full h-1/4;

}
.mobile-nav  li{
  @apply w-full h-full text-center border-b-2;
}
.project-desc{
  @apply absolute left-0 right-0 top-0 bottom-0 bg-secondary opacity-0 duration-500 hover:opacity-70;
}
.btn{
  @apply bg-sky-500 px-5 py-2 font-bold hover:border-2 border-white rounded;
}
html{
  scroll-behavior: smooth;
}